import { APPROVESTATUS } from "./GoalSettingCommon";
import { ExtensionData, Bean, Required } from "@allianzSND/core";

@Bean('GoalSettingGoalStatus')
export class GoalSettingGoalStatus {

    @Required
    private _DataYear: number;

    private _IsCurrent: string;

    @Required
    private _PersonnelGoalApplicableYM: number;

    @Required
    private _TeamGoalApplicableYM: number;

    @Required
    private _GoalSetMonth: number;

    @Required
    private _ApproveStatus: APPROVESTATUS;

    private _IsFirstTime: boolean;

    private _IsNeedSetting: boolean;

    private _RemainingDays: number;

    private _SupervisorComment: string;

    private _Extension: ExtensionData;

    constructor() {

    }

    public getExtension() {
        return this._Extension;
    }
    public setExtension(extension) {
        this._Extension = extension;
    }

    public get PersonnelGoalApplicableYM(): number {
        return this._PersonnelGoalApplicableYM;
    }
    public get TeamGoalApplicableYM(): number {
        return this._TeamGoalApplicableYM;
    }
    public get GoalSetMonth(): number {
        return this._GoalSetMonth;
    }
    public get ApproveStatus(): APPROVESTATUS {
        return this._ApproveStatus;
    }
    public get IsFirstTime(): boolean {
        return this._IsFirstTime;
    }
    public get IsNeedSetting(): boolean {
        return this._IsNeedSetting;
    }
    public get RemainingDays(): number {
        return this._RemainingDays;
    }
    public get DataYear(): number {
        return this._DataYear;
    }
    public get IsCurrent(): string {
        return this._IsCurrent;
    }
    public get SupervisorComment(): string {
        return this._SupervisorComment;
    }



    public set IsCurrent(value: string) {
        this._IsCurrent = value;
    }
    public set DataYear(value: number) {
        this._DataYear = value;
    }
    public set PersonnelGoalApplicableYM(value: number) {
        this._PersonnelGoalApplicableYM = value;
    }
    public set TeamGoalApplicableYM(value: number) {
        this._TeamGoalApplicableYM = value;
    }
    public set GoalSetMonth(value: number) {
        this._GoalSetMonth = value;
    }
    public set ApproveStatus(value: APPROVESTATUS) {
        this._ApproveStatus = value;
    }
    public set IsFirstTime(value: boolean) {
        this._IsFirstTime = value;
    }
    public set IsNeedSetting(value: boolean) {
        this._IsNeedSetting = value;
    }
    public set RemainingDays(value: number) {
        this._RemainingDays = value;
    }
    public set SupervisorComment(value: string) {
        this._SupervisorComment = value;
    }

}