import { Bean, Required } from "@allianzSND/core";

@Bean('MonthlyPlanFYFCData')
export class MonthlyPlanFYFCData {

    @Required
    private _Month: number;

    @Required
    private _Plan: string;

    @Required
    private _Actual: string;

    constructor(Month:number, Plan:string, Actual:string){
        this._Month = Month;
        this._Plan = Plan;
        this._Actual = Actual;
    }

    public get Month(): number {
        return this._Month;
    }

    public get Actual(): string {
        return this._Actual;
    }

    public get Plan(): string {
        return this._Plan;
    }
    
    public set Plan(value: string) {
        this._Plan = value;
    }

    public clone(): MonthlyPlanFYFCData{
        return new MonthlyPlanFYFCData(this._Month, this._Plan, this._Actual);
    }
}