import { TIMEBASE, Bean, Required } from "@allianzSND/core";

@Bean('ActivityValue')
export class ActivityValue{

    @Required
    private _TimeBase: TIMEBASE;

    @Required
    private _Find: string;

    @Required
    private _Schedule: string;

    @Required
    private _Meet: string;

    @Required
    private _Submit: string;
    
    @Required
    private _Inforce: string;
    
    constructor(TimeBase, Find, Schedule, Meet, Submit, Inforce) {
        this._TimeBase = TimeBase;
        this._Schedule = Schedule;
        this._Find = Find;
        this._Meet = Meet;
        this._Submit = Submit;
        this._Inforce = Inforce;
    }

    public get TimeBase(): TIMEBASE {
        return this._TimeBase;
    }
    public get Find(): string {
        return this._Find;
    }
    public get Schedule(): string {
        return this._Schedule;
    }
    public get Meet(): string {
        return this._Meet;
    }
    public get Submit(): string {
        return this._Submit;
    }
    public get Inforce(): string {
        return this._Inforce;
    }

}