import { Bean, Required } from "@allianzSND/core";

@Bean('ApproveInfo')
export class ApproveInfo{

    @Required
    private _DataYear: number;

    @Required
    private _AgentID: string;

    @Required
    private _IsApprove: boolean;
    
    @Required
    private _Comment: string;


    constructor(DataYear:number, AgentID: string, IsApprove:boolean, Comment:string){
        this._DataYear = DataYear;
        this._AgentID = AgentID;
        this._IsApprove = IsApprove;
        this._Comment = Comment;
    }

    public get Comment(): string {
        return this._Comment;
    }
    public get IsApprove(): boolean {
        return this._IsApprove;
    }
    public get AgentID(): string {
        return this._AgentID;
    }
    public get DataYear(): number {
        return this._DataYear;
    }

}