  public getActualPlanFromMonthList(list: Array<MonthlyPlanFYFCData>, actualMonth: number) {
    console.log("getActualPlanFromMonthList:", list, actualMonth);
    return list.map((x, index) => (index + 1 < actualMonth || index + 1 === actualMonth ? x.Actual : x.Plan));
  }

  public calculateANPFromFYFC(FYFC, rate): string {
    if (StringUtils.isEmpty(FYFC) || FYFC == this.DASH) {
      return this.DASH;
    } else {
      return this.numberToFix(Number(FYFC) * Number(rate), 0).toString();
    }
  }

  public calculateFYFCFromANP(ANP, rate): string {
    if (StringUtils.isEmpty(ANP) || ANP == this.DASH) {
      return this.DASH;
    } else {
      return this.numberToFix(Number(ANP) / Number(rate), 0).toString();
    }
  }

