  private async _pushApproveGoal(approveInfo: ApproveInfo): Promise<SuccessStatus> {
    let returnResp: SuccessStatus = new SuccessStatus(false);
    let approveInfoData = {
      "AgentID": approveInfo.AgentID,
      "DataYear": approveInfo.DataYear,
      "isApprove": approveInfo.IsApprove,
      "comment": approveInfo.Comment
    }
    try {
      let mainData = approveInfoData;
      let pushApproveData = this.APIFactory.getAPI("pushApproveGoal");
      (<pushApproveGoalAPI>pushApproveData).mainData = mainData;
      console.log('pushApproveGoal mainData', mainData);
      let getResp = await this.dispatcher.dispatch(pushApproveData).toPromise();
      console.log('getResp', getResp);
      if (getResp.success) {
        await this.dataSyncService.syncFunc(["DASHBOARD", "GOAL", "PROGRESS"]);
        returnResp.isSuccess = true;
      } else {
        throw "Approve Goal fail.";
      }
    } catch (error) {
      this.errorHandler.handleError(new APPError('F00100', 'pushApproveGoal fail!' + error.message));
    }
    console.log("[_pushApproveGoal] returnResp:", returnResp);
    return returnResp;

  }

  private async _pushGoalSetting(submitDatas: SubmitGoalData): Promise<SuccessStatus> {

    let returnResp: SuccessStatus = new SuccessStatus(false);
    try {

      console.log('pushGoalSettingDataAPI mainData', submitDatas);
      let pushGoalSettingData = this.APIFactory.getAPI("pushGoalSettingData");
      (<pushGoalSettingDataAPI>pushGoalSettingData).mainData = submitDatas;
      let getResp = await this.dispatcher.dispatch(pushGoalSettingData).toPromise();

      if (getResp.success) {
        returnResp.isSuccess = true;
      } else {
        throw "push Goal failed," + getResp.Header.msg;
      }

    } catch (error) {
      this.errorHandler.handleError(new APPError('F00101', error));
    }
    return returnResp;
  }

  private _converSelectCode(StepData: GoalSettingStepData): GoalSettingStepData {
    this.ColMapToProfile.forEach((code, col) => {
      let profileCodes: Array<ProfileCode> = this.ColMapToProfileCodeMap.get(col);
      if (profileCodes.length && StepData.Step1 && StepData.Step1[col]) {
        let colVal = StepData.Step1[col];
        profileCodes = profileCodes.filter(x => x.getCode() == colVal);
        StepData.Step1[col] = profileCodes.length ? profileCodes[0].getMappingID() : this.DASH;
      }
    });
    return StepData;
  }

