import { IMockAPI, IAPI, ISQLiteAPI, DaoFactory, ClientCustomDao, EqualRestriction } from '@allianzSND/core';
import { Observable } from 'rxjs';

export class AgencyPlanGetMainAPI implements IMockAPI, IAPI, ISQLiteAPI {
  private _dataYear: number;

  setDataYear(dataYear: number) {
    this._dataYear = dataYear
  }

  constructor(private daoFactory: DaoFactory) {

  }

  getAPIName(): string {
    return "getAgencyPlanMain";
  }

  getMockPath(): string {
    return "./assets/mock/getAgencyPlanMainMock.json";
  }

  executeSQL(): Observable<any> {
    console.log("dataYear", this._dataYear);
    return Observable.create((observer) => {
      let agencyMainObj = this.daoFactory.getDefaultTable("TW_LH_SD_VW_Agency_Plan_Main");
      let dao = this.daoFactory.getDefaultDao();
      if (agencyMainObj != undefined && dao != undefined) {
        dao = new ClientCustomDao(dao);
        agencyMainObj.addRestriction(new EqualRestriction('DataYear', [this._dataYear]));
        dao.queryByTable(agencyMainObj).subscribe((resp) => {
          console.log("resp", resp);
          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }
}
