import { Bean, Required } from "@allianzSND/core";

@Bean('AgencyPlanDataForOverview')
export class AgencyPlanDataForOverview {

    @Required
    private _FYFCForecast: string;

    @Required
    private _ANPForecast: string;

    @Required
    private _ManpowerPlan: string;

    @Required
    private _RecruitmentPlan: string;
    
    @Required
    private _CompletionRate: string;

    constructor() {       
    }

    public get FYFCForecast(): string {
        return this._FYFCForecast;
    }
    public set FYFCForecast(value: string) {
        this._FYFCForecast = value;
    }

    public get ANPForecast(): string {
        return this._ANPForecast;
    }
    public set ANPForecast(value: string) {
        this._ANPForecast = value;
    }
   
    public get ManpowerPlan(): string {
        return this._ManpowerPlan;
    }
    public set ManpowerPlan(value: string) {
        this._ManpowerPlan = value;
    }
   
    public get RecruitmentPlan(): string {
        return this._RecruitmentPlan;
    }
    public set RecruitmentPlan(value: string) {
        this._RecruitmentPlan = value;
    }

    public get CompletionRate(): string {
        return this._CompletionRate;
    }
    public set CompletionRate(CompletionRate: string) {
        this._CompletionRate = CompletionRate;
    }
    
    


}