import { IMockAPI, IAPI, ISQLiteAPI, DaoFactory, ClientCustomDao, EqualRestriction} from '@allianzSND/core';
import { Observable } from 'rxjs';

export class AgencyPlanGetDetailAPI implements IMockAPI, IAPI, ISQLiteAPI{
  private _dataYear: number;

  setDataYear(dataYear: number) {
    this._dataYear = dataYear;
  }

  constructor(private daoFactory: DaoFactory) {

  }

  getAPIName(): string {
    return "getAgencyPlanDetail";
  }

  getMockPath(): string {
    return "./assets/mock/getAgencyPlanDetail.json";
  }

  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let agencyDetailObj = this.daoFactory.getDefaultTable("TW_LH_SD_VW_Agency_Detail_Data");
      let dao = this.daoFactory.getDefaultDao();
      if (agencyDetailObj != undefined && dao != undefined) {

        dao = new ClientCustomDao(dao);
        agencyDetailObj.addRestriction(new EqualRestriction('DataYear', [this._dataYear]));
        dao.queryByTable(agencyDetailObj).subscribe((resp) => {
          console.log(resp);
          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }
}
