import { Bean, Required } from "@allianzSND/core";

@Bean('AgencyPlanMainData')
export class AgencyPlanMainData {

    @Required
    private _DataYear: number;

    @Required
    private _DataType: string;

    @Required
    private _Forecast: string;

    @Required
    private _Actual: string;
    
    @Required
    private _MonthPlan: string;

    constructor() {
    }

    public get DataYear(): number {
        return this._DataYear;
    }
    public set DataYear(value: number) {
        this._DataYear = value;
    }

    public get DataType(): string {
        return this._DataType;
    }
    public set DataType(value: string) {
        this._DataType = value;
    }

    public get Forecast(): string {
        return this._Forecast;
    }
    public set Forecast(value: string) {
        this._Forecast = value;
    }

    public get Actual(): string {
        return this._Actual;
    }
    public set Actual(value: string) {
        this._Actual = value;
    }

    public get MonthPlan(): string {
        return this._MonthPlan;
    }
    public set MonthPlan(value: string) {
        this._MonthPlan = value;
    }
}