import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { PlanFYFCPopupInfo } from './model/GoalSettingCommon';
import { DefaultLoginMgr, NotificationType } from '@allianzSND/core';

@Injectable({
    providedIn: 'root'
})
export class GoalStoreService {

    constructor(
        private loginMgr: DefaultLoginMgr
    ) {
        this.loginMgr.subscribeLogout().subscribe(() => {
            //reset to init
            this.setSyncGoalDatas(null);
            this.setGoalLandingStatus(GOAL_LANDING_STATUS.FIRST_INIT);
            this.setSettingYear(null);
            this.setIsPromo(false);
            this.setCanSkip(true);
            this.setPlanFYFCPopupInfo(new PlanFYFCPopupInfo(PLAN_FYFC_POPUP_STATE.CLOSE, null));
        })
    }

    private syncGoalDatas: SYNC_STATUS = null;
    private settingYear: number = null;
    private isPromo = false;
    private canSkip = true;
    private planFYFCPopupInfo: PlanFYFCPopupInfo = new PlanFYFCPopupInfo(PLAN_FYFC_POPUP_STATE.CLOSE, null);
    private landingStatus: GOAL_LANDING_STATUS = GOAL_LANDING_STATUS.FIRST_INIT;
    private yearSubject: BehaviorSubject<number> = new BehaviorSubject(this.settingYear);
    private statusSubject: BehaviorSubject<GOAL_LANDING_STATUS> = new BehaviorSubject(this.landingStatus);
    private isPromoSubject: BehaviorSubject<boolean> = new BehaviorSubject(this.isPromo);
    private canSkipSubject: BehaviorSubject<boolean> = new BehaviorSubject(this.canSkip);
    private planFYFCPopupInfoSubject: BehaviorSubject<PlanFYFCPopupInfo> = new BehaviorSubject(this.planFYFCPopupInfo);
    private syncGoalDatasSubject: BehaviorSubject<SYNC_STATUS> = new BehaviorSubject(this.syncGoalDatas);
    private goalPopupResponseSubject: Subject<GoalPopupResponse> = new Subject();


    public getSyncGoalDatas(): Observable<SYNC_STATUS> {
        return this.syncGoalDatasSubject.asObservable();
    }

    public setSyncGoalDatas(status: SYNC_STATUS): void {
        this.syncGoalDatas = status;
        this.syncGoalDatasSubject.next(this.syncGoalDatas);
    }

    public getGoalLandingStatus(): Observable<GOAL_LANDING_STATUS> {
        return this.statusSubject.asObservable();
    }

    public setGoalLandingStatus(status: GOAL_LANDING_STATUS): void {
        this.landingStatus = status;
        this.statusSubject.next(this.landingStatus);
    }

    public getSettingYear(): Observable<number> {
        return this.yearSubject.asObservable();
    }

    public setSettingYear(year: number): void {
        this.settingYear = year;
        this.yearSubject.next(this.settingYear);
    }

    public getIsPromo(): Observable<boolean> {
        return this.isPromoSubject.asObservable();
    }

    public setIsPromo(val: boolean): void {
        this.isPromo = val;
        this.isPromoSubject.next(this.isPromo);
    }

    public getCanSkip(): Observable<boolean> {
        return this.canSkipSubject.asObservable();
    }

    public setCanSkip(val: boolean): void {
        this.canSkip = val;
        this.canSkipSubject.next(this.canSkip);
    }

    public setPopupResp(resp: GoalPopupResponse): void {
        this.goalPopupResponseSubject.next(resp);
    }

    public getPopupResp(): Observable<GoalPopupResponse> {
        return this.goalPopupResponseSubject.asObservable();
    }

    // for edit personal popup

    public setPlanFYFCPopupInfo(info: PlanFYFCPopupInfo) {
        console.log("Set info");
        this.planFYFCPopupInfo = info;
        if (info.data) {
            this.planFYFCPopupInfo.data.Step4 = this.planFYFCPopupInfo.data.Step4.clone();
        }
        this.planFYFCPopupInfoSubject.next(this.planFYFCPopupInfo);
    }

    public getPlanFYFCPopupInfo() {
        return this.planFYFCPopupInfoSubject.asObservable();
    }

    // end of for edit personal popup

}

export class GoalPopupResponse {
    public response: boolean;
    public type: NotificationType;
}

export enum GOAL_LANDING_STATUS {
    SETTING_SUBMITTED,
    FIRST_INIT, // from landing
    FIRST_SET, // from overview and isn't adjsut
    ADJUST_GOAL // from overview and is adjust
}

export enum PLAN_FYFC_POPUP_STATE {
    DISPLAY,
    NEEDAPPROVESUBMIT,
    NOTNEEDAPPROVESUBMIT,
    CLOSE
}

export enum SYNC_STATUS {
    SYNC,
    FINISH
}



