import { Bean, Required } from "@allianzSND/core";

@Bean('GoalSettingTranslate')
export class GoalSettingTranslate {

    @Required
    private _TeamGoalEffectiveMonthTitle: string = '';

    @Required
    private _PersonalGoalEffectiveMonthTitle: string = '';

    @Required
    private _NowToYearEndTitle: string = '';

    @Required
    private _FYFCNowToYearEndTitle: string = '';

    @Required
    private _ANPNowToYearEndTitle: string = '';

    @Required
    private _PersonalFYFCActualVariableTitle: string = '';

    @Required
    private _TeamFYFCActualVariableTitle: string = '';

    @Required
    private _TeamANPActualVariableTitle: string = '';

    @Required
    private _FYFCActualDashTitle: string = '';
    
    constructor() {       
    }

    public get TeamGoalEffectiveMonthTitle(): string {
        return this._TeamGoalEffectiveMonthTitle;
    }
    public set TeamGoalEffectiveMonthTitle(value: string) {
        this._TeamGoalEffectiveMonthTitle = value;
    }
   
    public get PersonalGoalEffectiveMonthTitle(): string {
        return this._PersonalGoalEffectiveMonthTitle;
    }
    public set PersonalGoalEffectiveMonthTitle(value: string) {
        this._PersonalGoalEffectiveMonthTitle = value;
    }
   
    public get NowToYearEndTitle(): string {
        return this._NowToYearEndTitle;
    }
    public set NowToYearEndTitle(value: string) {
        this._NowToYearEndTitle = value;
    }
    
    public get FYFCNowToYearEndTitle(): string {
        return this._FYFCNowToYearEndTitle;
    }
    public set FYFCNowToYearEndTitle(value: string) {
        this._FYFCNowToYearEndTitle = value;
    }
    
    public get ANPNowToYearEndTitle(): string {
        return this._ANPNowToYearEndTitle;
    }
    public set ANPNowToYearEndTitle(value: string) {
        this._ANPNowToYearEndTitle = value;
    }
  
    public get PersonalFYFCActualVariableTitle(): string {
        return this._PersonalFYFCActualVariableTitle;
    }
    public set PersonalFYFCActualVariableTitle(value: string) {
        this._PersonalFYFCActualVariableTitle = value;
    }
    
    public get TeamFYFCActualVariableTitle(): string {
        return this._TeamFYFCActualVariableTitle;
    }
    public set TeamFYFCActualVariableTitle(value: string) {
        this._TeamFYFCActualVariableTitle = value;
    }
  
    public get TeamANPActualVariableTitle(): string {
        return this._TeamANPActualVariableTitle;
    }
    public set TeamANPActualVariableTitle(value: string) {
        this._TeamANPActualVariableTitle = value;
    }
    
    public get FYFCActualDashTitle(): string {
        return this._FYFCActualDashTitle;
    }
    public set FYFCActualDashTitle(value: string) {
        this._FYFCActualDashTitle = value;
    }
}