import { GoalSettingYearConfig } from "./GoalSettingYearConfig";
import { GoalSettingGoalStatus } from "./GoalSettingGoalStatus";
import { GoalSettingStep1 } from "./GoalSettingStep1";
import { GoalSettingStep2 } from "./GoalSettingStep2";
import { GoalSettingStep3 } from "./GoalSettingStep3";
import { GoalSettingStep4 } from "./GoalSettingStep4";
import { GoalSettingStep5 } from "./GoalSettingStep5";
import { AgencyPlanDetail } from "./AgencyPlanDetail";
import { AgencyPlanDataForOverview } from "./AgencyPlanDataForOverview";
import { Bean, Required } from "@allianzSND/core";

@Bean('GoalSettingStepData')
export class GoalSettingStepData{

    @Required
    private _DataYear: number;

    @Required
    private _YearConfig: GoalSettingYearConfig;

    @Required
    private _GoalStatus: GoalSettingGoalStatus;

    private _Step1: GoalSettingStep1;

    private _Step2: GoalSettingStep2;

    private _Step3: GoalSettingStep3;

    @Required
    private _Step4: GoalSettingStep4;

    private _Step5: GoalSettingStep5;

    private _AgencyPlan: AgencyPlanDataForOverview;

    private _ActualList: Array<string> = [];

    constructor(DataYear) {
        this._DataYear = DataYear;
    }
    
    public get DataYear(): number {
        return this._DataYear;
    }
    public get YearConfig(): GoalSettingYearConfig {
        return this._YearConfig;
    }
    public set YearConfig(value: GoalSettingYearConfig) {
        this._YearConfig = value;
    }
    public get GoalStatus(): GoalSettingGoalStatus {
        return this._GoalStatus;
    }
    public set GoalStatus(value: GoalSettingGoalStatus) {
        this._GoalStatus = value;
    }
    public get Step1(): GoalSettingStep1 {
        return this._Step1;
    }
    public set Step1(value: GoalSettingStep1) {
        this._Step1 = value;
    }
    public get Step2(): GoalSettingStep2 {
        return this._Step2;
    }
    public set Step2(value: GoalSettingStep2) {
        this._Step2 = value;
    }
    public get Step3(): GoalSettingStep3 {
        return this._Step3;
    }
    public set Step3(value: GoalSettingStep3) {
        this._Step3 = value;
    }
    public get Step4(): GoalSettingStep4 {
        return this._Step4;
    }
    public set Step4(value: GoalSettingStep4) {
        this._Step4 = value;
    }
    public get Step5(): GoalSettingStep5 {
        return this._Step5;
    }
    public set Step5(value: GoalSettingStep5) {
        this._Step5 = value;
    }
    public get AgencyPlan(): AgencyPlanDataForOverview {
        return this._AgencyPlan;
    }
    public set AgencyPlan(value: AgencyPlanDataForOverview) {
        this._AgencyPlan = value;
    }
    public get ActualList(): Array<string> {
        return this._ActualList;
    }
    public set ActualList(value: Array<string>) {
        this._ActualList = value;
    }




}