import { AgencyPlanMainData } from "./AgencyPlanMainData";
import { Bean, Required } from "@allianzSND/core";

@Bean('AgencyPlanMainInfo')
export class AgencyPlanMainInfo {

    @Required
    private _CompletionRate: string;
    
    @Required
    private _AgencyMainDataList: Array<AgencyPlanMainData>;

    constructor(){

    }
    
    public get CompletionRate(): string {
        return this._CompletionRate;
    }
    public set CompletionRate(value: string) {
        this._CompletionRate = value;
    }
    public get AgencyMainDataList(): Array<AgencyPlanMainData> {
        return this._AgencyMainDataList;
    }
    public set AgencyMainDataList(value: Array<AgencyPlanMainData>) {
        this._AgencyMainDataList = value;
    }


}