import { Bean, Required } from "@allianzSND/core";

@Bean('SettingChangeEvent')
export class SettingChangeEvent {

    @Required
    private _step: number;

    @Required
    private _column: string;

    @Required
    private _value: any;

    constructor(step: number, column: string, value: any) {
        this._step = step;
        this._column = column;
        this._value = value;
    }


    public get step(): number {
        return this._step;
    }
    public set step(value: number) {
        this._step = value;
    }

    public get column(): string {
        return this._column;
    }
    public set column(value: string) {
        this._column = value;
    }

    public get value(): any {
        return this._value;
    }
    public set value(value: any) {
        this._value = value;
    }
}