import { MonthlyPlanFYFCData } from "./MonthlyPlanFYFCData";
import { Bean, Required } from "@allianzSND/core";

@Bean('GoalSettingPlanPopup')
export class GoalSettingPlanPopup {

    @Required
    private _FYFCGoal: number;

    @Required
    private _actual: number;

    @Required
    private _performanceMonthStart: number;

    @Required
    private _performanceMonthEnd: number;

    @Required
    private _monthPlanList: Array<MonthlyPlanFYFCData>;

    @Required
    private _approveThreshold: number;
    
    @Required
    private _eachMonthPlan: Array<string> =[];

    constructor(FYFCgoal, actual, performanceMonthStart, performanceMonthEnd, monthPlanList) {
        this._FYFCGoal = FYFCgoal;
        this._actual = actual;
        this._performanceMonthStart = performanceMonthStart;
        this._performanceMonthEnd = performanceMonthEnd;
        this._monthPlanList = monthPlanList;
    }


    set FYFCGoal(FYFCgoal: number) {
        this._FYFCGoal = FYFCgoal;
    }

    set actual(value: number) {
        this._actual = value;
    }

    set performanceMonthStart(performanceMonthStart: number) {
        this._performanceMonthStart = performanceMonthStart;
    }

    set performanceMonthEnd(performanceMonthEnd: number) {
        this._performanceMonthEnd = performanceMonthEnd;
    }

    set monthPlanList(monthPlanList: Array<MonthlyPlanFYFCData>) {
        this._monthPlanList = monthPlanList;
    }

    set approveThreshold(approveThreshold: number) {
        this._approveThreshold = approveThreshold;
    }

    set eachMonthPlan(eachMonthPlan: Array<string>) {
        this._eachMonthPlan = eachMonthPlan;
    }

    get FYFCGoal() {
        return this._FYFCGoal;
    }

    get actual(): number {
        return this._actual;
    }

    get performanceMonthStart() {
        return this._performanceMonthStart;
    }

    get performanceMonthEnd() {
        return this._performanceMonthEnd;
    }

    get monthPlanList() {
        return this._monthPlanList;
    }

    get approveThreshold() {
        return this._approveThreshold;
    }

    get eachMonthPlan() {
        return this._eachMonthPlan;
    }

    public clone() {
        const cloneGoalSettingStep4 = new GoalSettingPlanPopup(this._FYFCGoal, this._actual, this._performanceMonthStart, this._performanceMonthEnd, this.monthPlanList.map(x => x.clone()));
        cloneGoalSettingStep4._eachMonthPlan = this.eachMonthPlan.map(x => x);
        cloneGoalSettingStep4._approveThreshold = this._approveThreshold;

        return cloneGoalSettingStep4;
    }
}