import { ActivityValue } from "./ActivityValue";
import { TIMEBASE, Bean, Required } from "@allianzSND/core";

@Bean('GoalSettingStep3')
export class GoalSettingStep3 {

    @Required
    private _ActivityValues: Array<ActivityValue> = [];

    @Required
    private _Activity: TIMEBASE;

    constructor() {
    }

    public get Activity(): TIMEBASE {
        return this._Activity;
    }
    public set Activity(value: TIMEBASE) {
        this._Activity = value;
    }
    public get ActivityValues(): Array<ActivityValue> {
        return this._ActivityValues;
    }
    public set ActivityValues(value: Array<ActivityValue>) {
        this._ActivityValues = value;
    }


}
