import { Bean, Required } from "@allianzSND/core";

@Bean('AgencyPlanAgentInfo')
export class AgencyPlanAgentInfo {

    @Required
    private _AgentID: string;

    @Required
    private _AgentName: string;

    @Required
    private _DataYear: number;
    
    @Required
    private _AppUseMode: string;

    constructor(AgentID: string, AgentName: string, DataYear: number, AppUseMode: string) {
        this._AgentID = AgentID;
        this._AgentName = AgentName;
        this._DataYear = DataYear;
        this._AppUseMode = AppUseMode;
    }

    public get AgentID(): string {
        return this._AgentID;
    }

    public get AgentName(): string {
        return this._AgentName;
    }
    
    public get DataYear(): number {
        return this._DataYear;
    }
    
    public get AppUseMode(): string {
        return this._AppUseMode;
    }

}
