import { IAPI, IMockAPI, DaoFactory, ISQLiteAPI, EqualRestriction, ClientCustomDao } from "@allianzSND/core";
import { Observable } from 'rxjs';

export class GoalSettingGetValueAPI implements IAPI, IMockAPI, ISQLiteAPI {


    constructor(private daoFactory: DaoFactory) {

    }

    private _dataYear: number = -1;
    setDataYear(dataYear: number) {
        this._dataYear = dataYear;
    }

    getAPIName(): string {
        return 'getGoalSettingValue';
    }

    getMockPath(): string {
        if (this._dataYear == 2019) {
            return './assets/mock/getGoalSettingValue_2019.json';
        }
        else if (this._dataYear == 2020) {
            return './assets/mock/getGoalSettingValue_2020.json';
        }
        else {
            console.warn("GoalSettingGetValueAPI mock path not found");
            return '';
        }

    }

    executeSQL(): Observable<any> {
        /*
            "FYFC": 200,
            "FYFC_Actual": 30,
            "FYFC_Now_To_Year_End": 170,
            "Manpower": 70,
            "Recruitment": 40
         */
        return Observable.create((observer) => {
            console.log("queryByTable: goalSettingValue this._dataYear:", this._dataYear);
            let dao = this.daoFactory.getDefaultDao();
            if (dao != undefined) {


                let goalSettingValue = this.daoFactory.getDefaultTable("TW_LH_SD_Goal_Setting_Value");
                if (goalSettingValue) {


                    dao = new ClientCustomDao(dao);
                    if (this._dataYear != -1) {
                        goalSettingValue.addRestriction(new EqualRestriction('DataYear', [this._dataYear]));
                    }


                    //todo!
                    dao.queryByTable(goalSettingValue).subscribe((resp) => {
                        console.log("queryByTable: goalSettingValue", resp);






                        // console.warn("goalSettingValue========: ",data);
                        observer.next(resp);
                        observer.complete();

                    });

                }


                else {
                    observer.next(false);
                    observer.complete();
                }

            }
            else {
                observer.next(false);
                observer.complete();
            }
        });
    }
}
