import { ActivityValue } from "./ActivityValue";
import { Bean, Required } from "@allianzSND/core";

@Bean('GoalSettingStep3ForTabChange')
export class GoalSettingStep3ForTabChange {

    @Required
    private _ActivityValue: ActivityValue = null;

    @Required
    private _Unit: string = '';
    
    constructor(ActivityValue: ActivityValue, Unit: string) {
        this._ActivityValue = ActivityValue;
        this._Unit = Unit;
    }

    public get ActivityValue() {
        return this._ActivityValue;
    }

    public get Unit() {
        return this._Unit;
    }
}