import { Bean, Required } from "@allianzSND/core";

@Bean('GoalSettingStep5')
export class GoalSettingStep5 {

    @Required
    private _TeamFYFC: string;

    @Required
    private _TeamANP: string;

    @Required
    private _Manpower: string;

    @Required
    private _Recruitment: string;

    @Required
    private _TeamFYFCActual: string;

    @Required
    private _TeamANPActual: string;

    @Required
    private _TeamFYFCNowToYearEnd: string;

    @Required
    private _TeamANPNowToYearEnd: string;


    constructor() {
    }

    public get TeamFYFC(): string {
        return this._TeamFYFC;
    }
    public set TeamFYFC(value: string) {
        this._TeamFYFC = value;
    }
    public get TeamANP(): string {
        return this._TeamANP;
    }
    public set TeamANP(value: string) {
        this._TeamANP = value;
    }
    public get Manpower(): string {
        return this._Manpower;
    }
    public set Manpower(value: string) {
        this._Manpower = value;
    }
    public get Recruitment(): string {
        return this._Recruitment;
    }
    public set Recruitment(value: string) {
        this._Recruitment = value;
    }
    public get TeamFYFCActual(): string {
        return this._TeamFYFCActual;
    }
    public set TeamFYFCActual(value: string) {
        this._TeamFYFCActual = value;
    }
    public get TeamANPActual(): string {
        return this._TeamANPActual;
    }
    public set TeamANPActual(value: string) {
        this._TeamANPActual = value;
    }
    public get TeamFYFCNowToYearEnd(): string {
        return this._TeamFYFCNowToYearEnd;
    }
    public set TeamFYFCNowToYearEnd(value: string) {
        this._TeamFYFCNowToYearEnd = value;
    }
    public get TeamANPNowToYearEnd(): string {
        return this._TeamANPNowToYearEnd;
    }
    public set TeamANPNowToYearEnd(value: string) {
        this._TeamANPNowToYearEnd = value;
    }


}
