import { MonthlyPlanFYFCData } from "./MonthlyPlanFYFCData";
import { Required, Bean } from "@allianzSND/core";

@Bean('GoalSettingStep4')
export class GoalSettingStep4 {

    @Required
    private _Forecast: string;

    @Required
    private _Shortfall: string;

    @Required
    private _Actual: string;

    @Required
    private _MonthList: Array<MonthlyPlanFYFCData> = [];

    constructor() {
    }

    public get Forecast(): string {
        return this._Forecast;
    }
    public set Forecast(value: string) {
        this._Forecast = value;
    }
    public get Shortfall(): string {
        return this._Shortfall;
    }
    public set Shortfall(value: string) {
        this._Shortfall = value;
    }
    public get Actual(): string {
        return this._Actual;
    }
    public set Actual(value: string) {
        this._Actual = value;
    }
    public get MonthList(): Array<MonthlyPlanFYFCData> {
        return this._MonthList;
    }
    public set MonthList(value: Array<MonthlyPlanFYFCData>) {
        this._MonthList = value;
    }

    public clone() {
        const cloneGoalSettingStep4 = new GoalSettingStep4();
        cloneGoalSettingStep4.Forecast = this._Forecast;
        cloneGoalSettingStep4.Shortfall = this._Shortfall;
        cloneGoalSettingStep4.MonthList = this._MonthList.map(x => x.clone());
        cloneGoalSettingStep4.Actual = this._Actual;
        return cloneGoalSettingStep4;
    }
}
