import { IAPI, IMockAPI, DaoFactory, SQLiteTable, ISQLiteAPI, EqualRestriction, StringUtils, ClientCustomDao, PageInfo, LimitRestriction, OffsetRestriction, OrderByRestriction } from "@allianzSND/core";
import { Observable } from 'rxjs';

export class GoalSettingGetYearConfigAPI implements IAPI, IMockAPI, ISQLiteAPI {


    constructor(private daoFactory: DaoFactory) {

    }

    private _dataYear: number = -1
    setDataYear(dataYear: number) {
        this._dataYear = dataYear;
    }

    getAPIName(): string {
        return 'getGoalSettingYearConfig';
    }

    getMockPath(): string {
        if(this._dataYear == 2019) {
            return './assets/mock/getGoalSettingYearConfig_2019.json';
        }

        else if(this._dataYear == 2020) {
            return './assets/mock/getGoalSettingYearConfig_2020.json';
        }

        else if(this._dataYear == -1) {
            return './assets/mock/getGoalSettingYearConfig_all.json';
        }

        else {
            console.warn("yearConfig dataYear not match");
            return ''
        }
    }

    executeSQL(): Observable<any> {
        return Observable.create((observer) => {
            let dao = this.daoFactory.getDefaultDao();
            if (dao != undefined) {

                let yearConfigObj = this.daoFactory.getDefaultTable("TW_LH_SD_VW_Year_Config");
                if (yearConfigObj) {

                    if(this._dataYear != -1) {
                        yearConfigObj.addRestriction(new EqualRestriction('DataYear',[this._dataYear]));
                    }

                    yearConfigObj.addRestriction(new OrderByRestriction([{ column: 'DataYear', order: 'ASC' }]));

                    dao = new ClientCustomDao(dao);           
                    
                    
                    dao.queryByTable(yearConfigObj).subscribe((resp) => {
                        console.warn("queryByTable yearConfig: ", JSON.stringify(resp));
                        observer.next(resp);
                        observer.complete();
                    });
                }
                else {
                    observer.next(false);
                    observer.complete();
                }

            }
            else {
                observer.next(false);
                observer.complete();
            }
        });
    }
}
