import { Bean, Required } from "@allianzSND/core";

export enum AGENCY_STATE {
    DISPLAY = 'display',
    ACCEPT = 'accept',
    REJECT = 'reject',
    FIRST = 'first'
}

@Bean('AgencyPlanStatus')
export class AgencyPlanStatus {

    @Required
    private _state: AGENCY_STATE;

    @Required
    private _agentID: string;

    constructor(state, agentID) {
        this.state = state;
        this.agentID = agentID;
    }

   
    public get state(): AGENCY_STATE {
        return this._state;
    }
    public set state(value: AGENCY_STATE) {
        this._state = value;
    }
    
    public get agentID(): string {
        return this._agentID;
    }
    public set agentID(value: string) {
        this._agentID = value;
    }
}