import { GoalTitleListItem } from "../../components/goal-ui-title-list/goa-ui-title-list-item";
import { Bean, Required } from "@allianzSND/core";

@Bean('CombineStep5AndAgecyPlanData')
export class CombineStep5AndAgecyPlanData {

    @Required
    private _Step5ItemList: Array<GoalTitleListItem> = [];

    @Required
    private _AgencyPlanItemList: Array<GoalTitleListItem> = [];
    
    @Required
    private _ItemTextIsRedList: Array<boolean> = [];

    constructor(Step5ItemList: Array<GoalTitleListItem>, AgencyPlanItemList: Array<GoalTitleListItem>, ItemTextIsRedList: Array<boolean>) {
        this._Step5ItemList = Step5ItemList;
        this._AgencyPlanItemList = AgencyPlanItemList;
        this._ItemTextIsRedList = ItemTextIsRedList;
    }

    public get Step5ItemList() {
        return this._Step5ItemList;
    }

    public get AgencyPlanItemList() {
        return this._AgencyPlanItemList;
    }

    public get ItemTextIsRedList() {
        return this._ItemTextIsRedList;
    }


}