import { Bean, Required } from "@allianzSND/core";

@Bean('IsApproveInfo')
export class IsApproveInfo{
    
    @Required
    private _IsNeedApprove: boolean;

    @Required
    private _IsPopup: boolean;

    private _PopupType: string;


    constructor(IsNeedApprove, IsPopup){
        this._IsNeedApprove = IsNeedApprove;
        this._IsPopup = IsPopup;
    }

    public get IsNeedApprove(): boolean {
        return this._IsNeedApprove;
    }
    public set IsNeedApprove(value: boolean) {
        this._IsNeedApprove = value;
    }
    public get IsPopup(): boolean {
        return this._IsPopup;
    }
    public set IsPopup(value: boolean) {
        this._IsPopup = value;
    }
    public get PopupType(): string {
        return this._PopupType;
    }
    public set PopupType(value: string) {
        this._PopupType = value;
    }

}