@Bean('ValidationState')
export class ValidationState {

    @Required
    private _step: number;

    @Required
    private _isDataLegal: boolean;
    
    constructor() {
    }

    set step(step: number) {
        this._step = step;
    }

    set isDataLegal(isDataLegal: boolean) {
        this._isDataLegal = isDataLegal;
    }

    get step() {
        return this._step;
    }

    get isDataLegal() {
        return this._isDataLegal;
    }
}

export class PlanFYFCPopupInfo {
    private _state: number;
    private _data: GoalSettingStepData;

    constructor(state: number, data: GoalSettingStepData) {
        this._state = state;
        this._data = data;
    }

    public set state(state: number) {
        this._state = state;
    }

    public set data(data: GoalSettingStepData) {
        this._data = data;
    }

    public get state(): number {
        return this._state;
    }

    public get data(): GoalSettingStepData {
        return this._data;
    }
}



