import { Bean, Required } from "@allianzSND/core";

@Bean('GoalSettingStep1')
export class GoalSettingStep1 {

    @Required
    private _FYFC: string;

    @Required
    private _AnnualConvention: string;

    @Required
    private _MDRT: string;

    @Required
    private _PromotionPlan: string;

    @Required
    private _Actual: string;

    @Required
    private _FYFCNowToYearEnd: string;

    private _ActivityFYFC: string;

    private _ActivityDays: string;

    constructor() {

    }

    public get FYFC(): string {
        return this._FYFC;
    }
    public set FYFC(value: string) {
        this._FYFC = value;
    }
    public get AnnualConvention(): string {
        return this._AnnualConvention;
    }
    public set AnnualConvention(value: string) {
        this._AnnualConvention = value;
    }
    public get MDRT(): string {
        return this._MDRT;
    }
    public set MDRT(value: string) {
        this._MDRT = value;
    }
    public get PromotionPlan(): string {
        return this._PromotionPlan;
    }
    public set PromotionPlan(value: string) {
        this._PromotionPlan = value;
    }
    public get Actual(): string {
        return this._Actual;
    }
    public set Actual(value: string) {
        this._Actual = value;
    }
    public get FYFCNowToYearEnd(): string {
        return this._FYFCNowToYearEnd;
    }
    public set FYFCNowToYearEnd(value: string) {
        this._FYFCNowToYearEnd = value;
    }
    public get ActivityFYFC(): string {
        return this._ActivityFYFC;
    }
    public set ActivityFYFC(value: string) {
        this._ActivityFYFC = value;
    }
    public get ActivityDays(): string {
        return this._ActivityDays;
    }
    public set ActivityDays(value: string) {
        this._ActivityDays = value;
    }

}
