import { Bean, Required } from "@allianzSND/core";

@Bean('AgencyPlanGoalExpected')
export class AgencyPlanGoalExpected {

    @Required
    private _ANP: string = '0';

    @Required
    private _DataYear: number = 0;

    @Required
    private _WorkingQuarter: number = 0;

    @Required
    private _FYFC: string = '0';

    @Required
    private _Q1: string = '0';

    @Required
    private _Q2: string = '0';

    @Required
    private _Q3: string = '0';

    @Required
    private _Q4: string = '0';

    @Required
    private _RecruitmentTotal: number = 0;
    
    @Required
    private _FYFCConvertANPRate: number;

    constructor(dataYear: number) {
        this._DataYear = dataYear;
    }

    public get ANP(): string {
        return this._ANP;
    }
    public set ANP(value: string) {
        this._ANP = value;
    }

    public get DataYear(): number {
        return this._DataYear;
    }

    public get WorkingQuarter(): number {
        return this._WorkingQuarter;
    }
    public set WorkingQuarter(value: number) {
        this._WorkingQuarter = value;
    }

    public get FYFC(): string {
        return this._FYFC;
    }
    public set FYFC(value: string) {
        this._FYFC = value;
    }
   
    public get Q1(): string {
        return this._Q1;
    }
    public set Q1(value: string) {
        this._Q1 = value;
    }
    
    public get Q2(): string {
        return this._Q2;
    }
    public set Q2(value: string) {
        this._Q2 = value;
    }
    
    public get Q3(): string {
        return this._Q3;
    }
    public set Q3(value: string) {
        this._Q3 = value;
    }
  
    public get Q4(): string {
        return this._Q4;
    }
    public set Q4(value: string) {
        this._Q4 = value;
    }
   
    public get RecruitmentTotal(): number {
        return this._RecruitmentTotal;
    }
    public set RecruitmentTotal(value: number) {
        this._RecruitmentTotal = value;
    }

    public get FYFCConvertANPRate(): number {
        return this._FYFCConvertANPRate;
    }
    public set FYFCConvertANPRate(value: number) {
        this._FYFCConvertANPRate = value;
    }

    public clone(): AgencyPlanGoalExpected {
        let cloneAgencyPlanGoalExpected: AgencyPlanGoalExpected = new AgencyPlanGoalExpected(this._DataYear);
        cloneAgencyPlanGoalExpected.WorkingQuarter = this._WorkingQuarter;
        cloneAgencyPlanGoalExpected.ANP = this._ANP;
        cloneAgencyPlanGoalExpected.FYFC = this._FYFC;
        cloneAgencyPlanGoalExpected.Q1 = this._Q1;
        cloneAgencyPlanGoalExpected.Q2 = this._Q2;
        cloneAgencyPlanGoalExpected.Q3 = this._Q3;
        cloneAgencyPlanGoalExpected.Q4 = this._Q4;
        cloneAgencyPlanGoalExpected.RecruitmentTotal = this._RecruitmentTotal;
        cloneAgencyPlanGoalExpected.FYFCConvertANPRate = this._FYFCConvertANPRate;
       
        return cloneAgencyPlanGoalExpected;

    }

    

}