import { Bean, Required } from "@allianzSND/core";

@Bean('GoalSettingStepDataParams')
export class GoalSettingStepDataParams{

    @Required
    private _DataYear: number;

    @Required
    private _isAdjust: boolean;

    constructor(DataYear: number, isAdjust: boolean){
        this._DataYear = DataYear;
        this._isAdjust = isAdjust;
    }

    public get DataYear(): number {
        return this._DataYear;
    }
    public set DataYear(value: number) {
        this._DataYear = value;
    }
    public get isAdjust(): boolean {
        return this._isAdjust;
    }
    public set isAdjust(value: boolean) {
        this._isAdjust = value;
    }


}