import { Bean, Required } from "@allianzSND/core";

@Bean('ValidError')
export class ValidError{

    @Required
    private _Step: number;

    @Required
    private _ColId: string;

    @Required
    private _Msg: string;

    constructor(Step: number, ColId: string, Msg:string){
        this._Step = Step;
        this._ColId = ColId;
        this._Msg = Msg;
    }

    public get Step(): number {
        return this._Step;
    }
    public get ColId(): string {
        return this._ColId;
    }
    public get Msg(): string {
        return this._Msg;
    }

}