import { IMockAPI, IAPI, ISQLiteAPI, DaoFactory, ClientCustomDao, EqualRestriction} from '@allianzSND/core';
import { Observable } from 'rxjs';

export class GoalSettingGetExpectedAPI implements IMockAPI, IAPI, ISQLiteAPI{
  private _dataYear: number;

  setDataYear(dataYear: number) {
    this._dataYear = dataYear
  }

  constructor(private daoFactory: DaoFactory) {

  }

  getAPIName(): string {
    return "getGoalSettingExpected";
  }

  getMockPath(): string {
    return "./assets/mock/getGoalSettingExpected.json";
  }

  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let goalExpectedObj = this.daoFactory.getDefaultTable("TW_LH_SD_VW_Goal_Setting_Expected");
      let dao = this.daoFactory.getDefaultDao();
      if (goalExpectedObj != undefined && dao != undefined) {
        dao = new ClientCustomDao(dao);
        goalExpectedObj.addRestriction(new EqualRestriction('DataYear', [this._dataYear]));
        dao.queryByTable(goalExpectedObj).subscribe((resp) => {
          console.log(resp);
          observer.next(resp);
          observer.complete();
        });
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }
}
