import { IAPI, EqualRestriction, ClientCustomDao } from "@allianzSND/core";
import { IMockAPI } from "@allianzSND/core";
import { ISQLiteAPI } from '@allianzSND/core';
import { Observable, from } from 'rxjs';
import { DaoFactory } from '@allianzSND/core';



export class GoalSettingGetActualAPI implements IAPI, IMockAPI, ISQLiteAPI {


  private _dataYear: number = -1;


  constructor(private daoFactory: DaoFactory) {

  }
  setDataYear(year: number) {
    this._dataYear = year;
  }



  getAPIName(): string {
    return 'getGoalSettingActualValue';
  }

  getMockPath(): string {
    //alert("personal progress mock data");
    if (this._dataYear == 2019) {
      return './assets/mock/getGoalSettingActualValue_2019.json';
    }
    else if (this._dataYear == 2020) {
      return './assets/mock/getGoalSettingActualValue_2020.json';
    }
    else {
      //todo throw error
      console.warn('dataYear not match mock path');
      return '';
    }
  }


  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let dao = this.daoFactory.getDefaultDao();
      if (dao != undefined) {


        let goalSettingObj = this.daoFactory.getDefaultTable("TW_LH_SD_VW_Actual_Value");
        if (goalSettingObj) {

          if (this._dataYear != -1) {
            goalSettingObj.addRestriction(new EqualRestriction('DataYear', [this._dataYear]));
          }

          dao = new ClientCustomDao(dao);


          dao.queryByTable(goalSettingObj).subscribe((resp) => {
            console.warn("queryByTable Goal_Setting_Actual_Value: ", resp);
            observer.next(resp);
            observer.complete();
          });
        }
        else {
          observer.next(false);
          observer.complete();
        }

      }
      else {
        observer.next(false);
        observer.complete();
      }
    });
  }

}